.app {
  margin-top: 10%;
  margin-bottom: 10%;
}
.item {
  transition: 0.2s;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.todo-label {
  font-size: 18px;
  display: block;
}
.item > .btn-group {
  display: block;
  transform: scale(0.8);
}

.filter {
  display: flex;
  justify-content: flex-end;
  padding-right: 25px;
}

.box {
  margin: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  border-radius: 2px;
  padding: 5px 10px 5px 15px;
}

.box-sm {
  padding: 2px 10px;
  box-shadow: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.complite-todo .todo-label {
  text-decoration: line-through;
}
.complite-todo {
  opacity: 0.4;
}
